.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  /* min-height: 100vh; */
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }

  to {
    transform: rotate(360deg);
  }
}
h1{
  text-align: center;
}

body {
  background: rgb(2, 0, 36);
  background: radial-gradient(circle,
      rgba(2, 0, 36, 1) 0%,
      rgba(30, 54, 59, 1) 0%,
      rgba(93, 93, 116, 1) 100%);
}

.container {
  display: flex !important;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  height: 100vh;
  max-width: 500px;
  width: 100%;
  margin: auto;
}

.container>form {
  width: 70%;
  border: 1px solid #dfdfdf;
  padding: 20px;
  border-radius: 5px;
  background: #fff;
}

pre {
  width: 70%;
  color: #fff;
  font-size: larger;
}

.cardContent {
  margin: 10px;
}

.card {
  width: 500px;
}

p {
  color: red;
}

.cardContainer {
  display: grid;
  margin: 10px;
  background: aliceblue;
  padding: 10px;
}
.cardContainerReg{
  display: grid;
  margin: 10px;
  padding: 10px;
}

.alignCenter {
  text-align: center;
  margin: 5px;
}

.label {
  font-size: 25px !important;
  font-family: emoji !important;
}